import React from "react";
import {
  CartInfoWrapper,
  CartInfoContents,
  CartInfoTitle,
  TextWrapper,
  CartInfoText,
} from "./CartInfo.styles";
import { PaymentButton } from "components";
import { connect } from "react-redux";
import { createStructuredSelector } from "reselect";
import { selectCartItems, selectCartTotal } from "store/cart/cartSelectors";

const CartInfo = ({ total }: any) => {
  const discountAmount = (total * 20) / 100;
  const totalAmount = total - discountAmount;

  return (
    <CartInfoWrapper>
      <CartInfoContents>
        <CartInfoTitle></CartInfoTitle>
        <TextWrapper>
          <CartInfoText> </CartInfoText>
          <CartInfoText>{`${total.toLocaleString("ko-KR")} `}</CartInfoText>
        </TextWrapper>
        <TextWrapper>
          <CartInfoText>  </CartInfoText>
          <CartInfoText>
            {total > 40000
              ? `- ${discountAmount.toLocaleString("ko-KR")} `
              : "0 "}
          </CartInfoText>
        </TextWrapper>
      </CartInfoContents>
      <PaymentButton total={totalAmount} />
    </CartInfoWrapper>
  );
};

const mapStateToProps = createStructuredSelector({
  cartItems: selectCartItems,
  total: selectCartTotal,
});

export default connect(mapStateToProps)(CartInfo);
