import React, { useState } from "react";
import { useHistory } from "react-router-dom";
import logo from "assets/images/logo.png";
import {
  LoginFormWrapper,
  LogoWrapper,
  LogoTitle,
  LogoImg,
  LoginFormH3,
  LoginFormH5,
  FormWrapper,
  InputWrapper,
  Label,
  Input,
  ErrorMessage,
  LoginBtn,
  SignUpRouteWrapper,
  SignUpRouteText,
  SignUpRouteLink,
} from "./LoginForm.styles";
import { useInput, useToast } from "hooks";
import { auth } from "core/utils/firebase/firebase";

function LoginForm() {
  const [emailId, setEmailId, onChangeEmailId] = useInput<string>("");
  const [password, setPassword, onChangePassword] = useInput<string>("");
  const [error, setError] = useState<string>("");
  const history = useHistory();
  const { toast } = useToast();

  const handleSubmit = async (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault();

    try {
      await auth.signInWithEmailAndPassword(emailId, password);
      setEmailId("");
      setPassword("");
      toast(" .", "bottom-center", 1200, 500);
      history.push("/");
    } catch (error) {
      console.log("error", error);
      setError(`  ,   .`);
    }
  };

  return (
    <LoginFormWrapper>
      <LogoWrapper>
        <LogoTitle to="/">
          <LogoImg src={`${logo}`} alt="  " />
          <LoginFormH3>GOPIZZA</LoginFormH3>
        </LogoTitle>
      </LogoWrapper>
      <LoginFormH5>
               
        .
      </LoginFormH5>
      <FormWrapper onSubmit={handleSubmit}>
        <InputWrapper>
          <Label> </Label>
          <Input
            type="email"
            placeholder="  "
            value={emailId}
            onChange={onChangeEmailId}
            required
          />
        </InputWrapper>
        <InputWrapper>
          <Label></Label>
          <Input
            type="password"
            placeholder=" "
            value={password}
            onChange={onChangePassword}
            required
          />
        </InputWrapper>
        <ErrorMessage>{error}</ErrorMessage>
        <LoginBtn type="submit" value="" />
      </FormWrapper>
      <SignUpRouteWrapper>
        <SignUpRouteText> ?</SignUpRouteText>
        <SignUpRouteLink to="/signUp"></SignUpRouteLink>
      </SignUpRouteWrapper>
    </LoginFormWrapper>
  );
}

export default LoginForm;
