/* eslint-disable @typescript-eslint/no-unused-vars */
import React from "react";
import { useHistory } from "react-router-dom";
import { PaymentBtn } from "./PaymentButton.styles";
import { Modal } from "components";
import { useSelector } from "react-redux";
import { RootState } from "store/type";
import { useModal, useToast } from "hooks";

declare global {
  interface Window {
    IMP: any;
  }
}

interface PaymentButtonProps {
  total: number;
}

const PaymentButton = ({ total }: PaymentButtonProps) => {
  const user = useSelector((state: RootState) => state.user.currentUser);
  const { isShown, toggle } = useModal();
  const { toast } = useToast();
  const history = useHistory();

  const handleMovePage = () => {
    history.push("/login");
  };

  const onClickPayment = () => {
    if (user === null) {
      toggle();
      return;
    }

    const { IMP } = window;
    IMP.init("imp33944797");

    const data = {
      pg: "html5_inicis", // PG
      pay_method: "card", // 
      merchant_uid: `mid_${new Date().getTime()}`, // 
      amount: total, // 
      name: ":   ", // 
      buyer_name: `${user.email}`, //  
      buyer_tel: "01012341234", //  
      buyer_email: `${user.email}`, //  
      buyer_addr: " 661-16", //  
      buyer_postcode: "06018", //  
    };

    IMP.request_pay(data, callback);
  };

  const callback = (rsp: any) => {
    if (rsp.success) {
      toast(" ", "bottom-center", 1200, 300);
      let msg: string = " .";
      msg += "ID : " + rsp.imp_uid;
      msg += " ID : " + rsp.merchant_uid;
      msg += "  : " + rsp.paid_amount;
      msg += "  : " + rsp.apply_num;
    } else {
      toast(` : ${rsp.error_msg}.`, "bottom-center", 1200, 500);
    }
  };

  return (
    <>
      <PaymentBtn onClick={onClickPayment}>{`${total.toLocaleString(
        "ko-KR"
      )} `}</PaymentBtn>
      <Modal
        isShown={isShown}
        hide={toggle}
        contentText=" !"
        buttonText=""
        onClick={handleMovePage}
      />
    </>
  );
};

export default PaymentButton;
