import React from "react";
import {
  StoreListContainer,
  StoreListWrapper,
  StoreListTitle,
  StoreListSubTitle,
  StoreListTable,
  StoreListHeader,
  HeaderItem,
  StoreListBody,
  BodyItem,
  BodyItemInfo,
} from "./StoreList.styles";

interface MapListProps {
  totalPosts: number;
  currentPosts?: StoreList[];
}

interface StoreList {
  id: number;
  name: string;
  address: string;
  tel: string;
  hours: string;
  latlng: number[];
}

const StoreList = ({ totalPosts, currentPosts }: MapListProps) => {
  return (
    <StoreListContainer>
      <StoreListWrapper>
        <StoreListTitle></StoreListTitle>
        <StoreListSubTitle>
           <span>{totalPosts}</span>  .
        </StoreListSubTitle>
        <StoreListTable>
          <StoreListHeader>
            <HeaderItem width={10}>NO</HeaderItem>
            <HeaderItem width={20}></HeaderItem>
            <HeaderItem width={30}></HeaderItem>
            <HeaderItem width={20}> </HeaderItem>
            <HeaderItem width={20}></HeaderItem>
          </StoreListHeader>
          <StoreListBody>
            {currentPosts?.map((store: StoreList, idx: number) => {
              return (
                <BodyItem key={idx}>
                  <BodyItemInfo width={10}>{store.id}</BodyItemInfo>
                  <BodyItemInfo width={20}>{store.name}</BodyItemInfo>
                  <BodyItemInfo width={30}>{store.address}</BodyItemInfo>
                  <BodyItemInfo width={20}>{store.tel}</BodyItemInfo>
                  <BodyItemInfo width={20}>{store.hours}</BodyItemInfo>
                </BodyItem>
              );
            })}
          </StoreListBody>
        </StoreListTable>
      </StoreListWrapper>
    </StoreListContainer>
  );
};

export default StoreList;
