import React from "react";
import {
  CartListBtn,
  CartListTitle,
  CartListWrapper,
  TitleWrapper,
} from "./CartList.styles";
import { CartItem, Modal } from "components";
import { clearItem } from "store/cart/cart";
import { useDispatch } from "react-redux";
import { useModal } from "hooks";
import { CartItemType } from "types/cart.types";

interface CartItemsProps {
  cartItems: CartItemType[];
}

const CartList = ({ cartItems }: CartItemsProps) => {
  const { isShown, toggle } = useModal();
  const dispatch = useDispatch();

  const handleShowModal = () => {
    toggle();
  };

  const handleClearFromCart = () => {
    dispatch(clearItem());
  };

  return (
    <CartListWrapper>
      <TitleWrapper>
        <CartListTitle>  </CartListTitle>
        <CartListBtn onClick={handleShowModal}></CartListBtn>
      </TitleWrapper>
      {cartItems.map((cartItem) => {
        return (
          <CartItem
            key={`${cartItem.id} ${cartItem.name}`}
            cartItem={cartItem}
          />
        );
      })}
      <Modal
        isShown={isShown}
        hide={toggle}
        contentText="  ?"
        buttonText=""
        onClick={handleClearFromCart}
      />
    </CartListWrapper>
  );
};

export default CartList;
