import React, { useRef } from "react";
import logo from "assets/images/logo.png";
import {
  SignUpFormContainer,
  LogoWrapper,
  LogoTitle,
  LogoImg,
  SignUpFormH3,
  SignUpFormWrapper,
  FormWrapper,
  InputWrapper,
  Label,
  Input,
  SignUpBtn,
  ErrorMessage,
  LoginRouteWrapper,
  LoginRouteText,
  LoginRouteLink,
} from "./SignUpForm.styles";
import { useForm } from "react-hook-form";
import { auth } from "core/utils/firebase/firebase";
import { useToast } from "hooks";
import { useHistory } from "react-router-dom";

interface InputTypes {
  email: string;
  password: string;
  password_confirm: string;
}

const SignUpForm = () => {
  const {
    register,
    watch,
    formState: { errors },
    handleSubmit,
  } = useForm<InputTypes>();

  const password = useRef<string | number>();
  password.current = watch("password");
  const { toast } = useToast();
  const history = useHistory();

  const onSubmit = async (data: InputTypes) => {
    const { email, password } = data;

    try {
      const res = await auth.createUserWithEmailAndPassword(email, password);
      if (res) {
        toast(" .", "bottom-center", 1200, 1000);
        history.push("/login");
      }
    } catch (error) {
      toast(`  .`, "bottom-center", 1500, 0);
    }
  };

  return (
    <SignUpFormContainer>
      <SignUpFormWrapper onSubmit={handleSubmit(onSubmit)}>
        <LogoWrapper>
          <LogoTitle to="/">
            <LogoImg src={`${logo}`} alt="  " />
            <SignUpFormH3>GOPIZZA</SignUpFormH3>
          </LogoTitle>
        </LogoWrapper>
        <FormWrapper>
          <InputWrapper>
            <Label>()</Label>
            <Input
              type="email"
              placeholder="() ."
              {...register("email", { required: true, pattern: /^\S+@\S+$/i })}
            />
            {errors.email && errors.email.type === "required" && (
              <ErrorMessage>  .</ErrorMessage>
            )}
            {errors.email && errors.email.type === "pattern" && (
              <ErrorMessage>   .</ErrorMessage>
            )}
          </InputWrapper>
          <InputWrapper>
            <Label></Label>
            <Input
              type="password"
              placeholder=" ."
              {...register("password", {
                required: true,
                pattern: /^(?=.*[A-Za-z])(?=.*\d)(?=.*[$@$!%*#?&])[A-Za-z\d$@$!%*#?&]{8,}$/,
              })}
            />
            {errors.password && errors.password.type === "required" && (
              <ErrorMessage>  .</ErrorMessage>
            )}
            {errors.password && errors.password.type === "pattern" && (
              <ErrorMessage>
                , ,   8  .
              </ErrorMessage>
            )}
          </InputWrapper>
          <InputWrapper>
            <Label> </Label>
            <Input
              type="password"
              placeholder="  ."
              {...register("password_confirm", {
                required: true,
                validate: (value) => value === password.current,
              })}
            />
            {errors.password_confirm &&
              errors.password_confirm.type === "required" && (
                <ErrorMessage>   .</ErrorMessage>
              )}
            {errors.password_confirm &&
              errors.password_confirm.type === "validate" && (
                <ErrorMessage>  .</ErrorMessage>
              )}
          </InputWrapper>
          <SignUpBtn type="submit" value="" />
        </FormWrapper>
      </SignUpFormWrapper>
      <LoginRouteWrapper>
        <LoginRouteText>  ?</LoginRouteText>
        <LoginRouteLink to="/login"></LoginRouteLink>
      </LoginRouteWrapper>
    </SignUpFormContainer>
  );
};

export default SignUpForm;
